import $ from 'jquery';
import './App.css';
import React   from 'react';
import Header from './header';
import Finger from 'fingerprintjs';
class Example extends React.Component {
    constructor(props)
	{
	  super(props);
	  this.state=
	  {
          list:[],
        color:'#fcad03',
        msg:'Generating Leader Board, Please Wait...',
        loggedin:localStorage.userId==null?false:true,
        gotList:false
	  }
    }
    componentWillMount(){
        if(this.state.loggedin===true){
        let finger = new Finger().get();
        let data={
            client:finger,
            userid:localStorage.userId
        }
        let pointer=this;
        $.post("http://10.10.108.240:4000/users/isLoggedIn",data,(data,status)=>{
            if(data.loggedin===false){
                localStorage.userId=null;
                pointer.setState({
                    loggedin:false
                });
                window.location.href="/";                
            }
            else{
                pointer.getList();
            }
        })}
        else{
            window.location.href="/";
        }
    }
    getList(){
        if(this.state.loggedin===true){
            let finger = new Finger().get();
            let data={
                client:finger,
                userid:localStorage.userId,
                contestId:this.props.match.params.id
            }
            let pointer=this;
            $.post("http://10.10.108.240:4000/contest/getLeaderBoard",data,(data,status)=>{
                if(data.error){
                    pointer.setState({
                        gotList:false,
                        msg:data.msg,
                        color:'red'
                    })
                }
                else{
                    if(data.list.length===0){
                        pointer.setState({
                            gotList:false,
                            msg:"No Participants Yet !!!",
                            color:'red'
                        });
                    }
                    else
                    pointer.setState({
                        gotList:true,
                        list:data.list
                    })
                }
            })
        }
        
    }
    render(){
        return(
            <div>
                <Header/>
                <div className='container' style={{marginTop:'75px'}}>
                    {!this.state.gotList?<div style={{paddingLeft:'10%',paddingRight:'10%'}}><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
                                    <h1 style={{textAlign:'center',color:this.state.color}}>{this.state.msg}</h1>
                                    <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
                    </div>:<div>
                    <center><h1 className='headings'>Welcome to LeaderBoard of {this.props.match.params.id}</h1></center>
                    <table class="table">
                        <thead>
                            <tr>
                            <th scope="col">Rank</th>
                            <th scope="col">User Name</th>
                            <th scope="col">Score</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.list.map((each,index)=>
                                <tr>
                                <th scope="row">{index+1}</th>
                                <td>{each.name}</td>
                                <td>{each.score}</td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                    </div>
                    }
                </div>
            </div>
        )
    }
}
export default Example;