import $ from 'jquery';
import './App.css';
import React   from 'react';
import Header from './header';
import Finger from 'fingerprintjs';
class Example extends React.Component {
    
    constructor(props)
	{
	  super(props);
	  this.state=
	  {
          score:null,
          solId:null,
          result:null,
          color:'white',
          exe:0
	  }
	}
    componentDidMount(){
        document.getElementById('langc').checked=true;
        $("textarea").keydown(function(e) {
            if(e.keyCode === 9) { // tab was pressed
                // get caret position/selection
                var start = this.selectionStart;
                var end = this.selectionEnd;
        
                var $this = $(this);
                var value = $this.val();
        
                // set textarea value to: text before caret + tab + text after caret
                $this.val(value.substring(0, start)
                            + "\t"
                            + value.substring(end));
        
                // put caret at right position again (add one for the tab)
                this.selectionStart = this.selectionEnd = start + 1;
        
                // prevent the focus lose
                e.preventDefault();
            }
        });
    }

    run(){
        this.setState({
            exe:1
        })
        let finger = new Finger().get();
        let data={
            client:finger,
            userid:localStorage.userId,
            code:document.getElementById("code").value,
            lang:document.querySelector('input[name="lang"]:checked').value,
            contest:this.props.match.params.id,
            ques:this.props.match.params.ques
        }
        let pointer=this;
        $.post("http://10.10.108.240:4000/solution/submit",data,(data,status)=>{
              if(data.error){
                alert(data.msg);
               }
               else{ 
                
                pointer.setState({
                    score:data.score,
                    solId:data.solId,
                    result:data.result,
                    exe:0
                });
               }
        });
    }
    render(){
        return(
            <div style={{marginTop:'75px'}}>
                <Header/>
                <center><h1 className='headings'>Welcome to YourCode Compiler</h1></center>
                <div className='col-md-6'>
                    <h3>Your Code :</h3>
                    <textarea style={{height:'600px',width:'100%',padding:'10px',borderRadius:'10px',border:'solid',borderWidth:'1px'}} id='code'></textarea>
                </div>
                <div className='col-md-6'>
                    
                    <div className='col-sm-6'>
                        <h3>Your Language :</h3>
                        <p style={{paddingTop:'10px'}}>
                        <input type="radio" id="langc" name='lang' value="c" /> C<br/><br/>
                        <input type="radio" id="langcpp" name='lang' value="cpp"/> C++ 14<br/><br/>
                        <input type="radio" id="langpy2" value="py2" name='lang'/> Python 2.7<br/><br/>
                        <input type="radio" id="langpy3" name='lang' value="py3"/> Python 3.6<br/><br/>
                        <input type="radio" id="langjava" value="java" name='lang'/> Java
                        </p><br/>
                        <div onClick={()=>{this.run()}} className={'btn btn-primary'+(this.state.exe===0?'':' disabled')} style={{backgroundColor:'#337ab7',letterSpacing:'3px',fontSize:'20px'}}><i class="far fa-play-circle"></i> {this.state.exe===0?'Submit':'Evaluating...'}</div>
                    </div>
                    <div className='col-sm-12'style={{marginTop:'10px'}}>
                        <h3>Your Output :</h3>
                        
                        <table class="table">
                            <thead>
                                <tr>
                                <th scope="col">Solution id</th>
                                <th scope="col">Score</th>
                                <th scope="col">Result</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                <th scope="row">{this.state.solId}</th>
                                <td>{this.state.score}</td>
                                <td>{this.state.result==='AC'?<i style={{color:'green'}}>{this.state.result}</i>:<i style={{color:'red'}}>{this.state.result}</i>}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <p className='col-sm-12'style={{marginTop:'10px'}} >NOTE :- You can check all your solutions at your profile.</p>
                </div>
            </div>
        )
    }
}
export default Example;