import $ from 'jquery';
import {CSSTransition} from 'react-transition-group';
import './App.css';
import React   from 'react';
import Header from './header';
import Finger from 'fingerprintjs';
class Example extends React.Component {
    constructor(props)
	{
	  super(props);
	  this.state=
	  {
          runningContests:[],
          prevContests:[],
          futContests:[],
        loggedin:localStorage.userId==null?false:true
	  }
    }
    componentWillMount(){
        if(this.state.loggedin===true){
        let finger = new Finger().get();
        let data={
            client:finger,
            userid:localStorage.userId
        }
        let pointer=this;
        $.post("http://10.10.108.240:4000/users/isLoggedIn",data,(data,status)=>{
            if(data.loggedin===false){
                localStorage.userId=null;
                pointer.setState({
                    loggedin:false
                });
                window.location.href="/";                
            }
            else{
                pointer.getList();
            }
        })}
        else{
            window.location.href="/";
        }
    }
    getList(){
        if(this.state.loggedin===true){
            let finger = new Finger().get();
            let data={
                client:finger,
                userid:localStorage.userId
            }
            let pointer=this;
            $.post("http://10.10.108.240:4000/contest/List",data,(data,status)=>{
                if(data.error){
                    localStorage.userId=null;
                    pointer.setState({
                        loggedin:false
                    });
                    window.location.href="/"; 
                }
                else{
                    let present=new Date();
                    let list1=[];
                    let list2=[];
                    let list3=[];
                    if(data.contests==null){
                        data.contests=[]
                    }
                    data.contests.forEach(con=>{
                        con.conSt=new Date(con.startDate+' '+con.startTime);
                        con.conEnd=new Date(con.endDate+' '+con.endTime);
                        if(present<con.conSt){
                            list2.push(con);
                        }
                        else if(present>con.conEnd){
                            list3.push(con);
                        }
                        else
                        list1.push(con);
                    })
                    pointer.setState({
                        runningContests:list1,
                        prevContests:list3,
                        futContests:list2
                    })
                }
            })
        }
        
    }
    render(){
        return(
            <div>
                <Header/>
                <div className='container' style={{marginTop:'75px'}}>
                    {
                        this.state.runningContests.length===0?null:
                        <>
                            <h2>Ongoing Contests</h2><div className='row'>
                            {
                                this.state.runningContests.map(con=>
                                    <CSSTransition
                                    in={true}
                                    key={con.contestId}
                                    appear={true}
                                    timeout={500}
                                    classNames="fade">
                                    <div className="col-sm-6 col-md-6 cont" style={{"position":"relative","float":"left"}} >
                                    <a href={'./contest/'+con.contestId}><div className="thumbnail" style={{backgroundColor:'#9f80c4'}}>
                                            <div className="row">
                                                <div className="col-sm-12"><div className="crop"><img src={con.img} alt={con.name}  className="image"/></div></div>
                                                <div className="col-sm-12">
                                                    <h3 style={{color:'white'}}>{con.name}</h3>
                                                    <p style={{color:'black',fontWeight:'500'}}>Organised by {con.org}</p>
                                                    <p>Started at :<br/>{String(con.conSt)}</p>
                                                    <p>Ends at :<br/>{String(con.conEnd)}</p>
                                                </div>
                                            </div>
                                        </div></a>
                                    </div></CSSTransition>
                                    )
                            }</div>
                        </>
                    }<br/><br/>
                    {
                        this.state.futContests.length===0?null:
                        <>
                            <h2>Upcoming Contests</h2><div className='row'>
                            {
                                this.state.futContests.map(con=>
                                    <CSSTransition
                                    in={true}
                                    key={con.contestId}
                                    appear={true}
                                    timeout={500}
                                    classNames="fade">
                                    <div className="col-sm-6 col-md-6 cont" style={{"position":"relative","float":"left"}} >
                                        <a href={'./contest/'+con.contestId}><div className="thumbnail" style={{backgroundColor:'#9f80c4'}}>
                                            <div className="row">
                                                <div className="col-sm-12"><div className="crop"><img src={con.img} alt={con.name}  className="image"/></div></div>
                                                <div className="col-sm-12">
                                                    <h3 style={{color:'white'}}>{con.name}</h3>
                                                    <p style={{color:'black',fontWeight:'500'}}>Organised by {con.org}</p>
                                                    <p>Started at :<br/>{String(con.conSt)}</p>
                                                    <p>Ends at :<br/>{String(con.conEnd)}</p>
                                                </div>
                                            </div>
                                        </div></a>
                                    </div></CSSTransition>
                                    )
                            }</div>
                        </>
                    }<br/><br/>
                    {
                        this.state.prevContests.length===0?null:
                        <>
                            <h2>Previous Contests</h2><div className='row'>
                            {
                                this.state.prevContests.map(con=>
                                    <CSSTransition
                                    in={true}
                                    key={con.contestId}
                                    appear={true}
                                    timeout={500}
                                    classNames="fade">
                                    <div className="col-sm-6 col-md-6 cont" style={{"position":"relative","float":"left"}} >
                                    <a href={'./contest/'+con.contestId}><div className="thumbnail" style={{backgroundColor:'#9f80c4'}}>
                                            <div className="row">
                                                <div className="col-sm-12"><div className="crop"><img src={con.img} alt={con.name}  className="image"/></div></div>
                                                <div className="col-sm-12">
                                                    <h3 style={{color:'white'}}>{con.name}</h3>
                                                    <p style={{color:'black',fontWeight:'500'}}>Organised by {con.org}</p>
                                                    <p>Started at :<br/>{String(con.conSt)}</p>
                                                    <p>Ends at :<br/>{String(con.conEnd)}</p>
                                                </div>
                                            </div>
                                        </div></a>
                                    </div></CSSTransition>
                                    )
                            }</div>
                        </>
                    }<br/><br/>
                </div>
            </div>
        )
    }
}
export default Example;