import $ from 'jquery';
import {CSSTransition} from 'react-transition-group';
import './App.css';
import React   from 'react';
import Login from './login';
import Signup from './signup';
import Finger from 'fingerprintjs';
class Example extends React.Component {
    constructor(props)
	{
	  super(props);
	  this.state=
	  {
	  	showLogin:false,
        showSignUp:false,
        loggedin:localStorage.userId==null?false:true,
        username:''
	  }
    }
    componentWillMount(){
        if(this.state.loggedin===true){
        let finger = new Finger().get();
        let data={
            client:finger,
            userid:localStorage.userId
        }
        let pointer=this;
        $.post("http://10.10.108.240:4000/users/isLoggedIn",data,(data,status)=>{
            if(data.loggedin===false){
                localStorage.userId=null;
                pointer.setState({
                    loggedin:false
                })                
            }
            else{
                pointer.setState({
                    username:data.username
                })
            }
        })}
    }
	toggleLogin()
	{
        if(this.state.loggedin===false)
        this.setState({
			showLogin:!this.state.showLogin,
			showSignUp:false
		})
	}
	toggleSignUp()
	{
        if(this.state.loggedin===false)
        this.setState({
			showSignUp:!this.state.showSignUp,
			showLogin:false
		})
    }
    toggleLogout(){
        if(this.state.loggedin===true){
            let finger = new Finger().get();
            let data={
                client:finger,
                userid:localStorage.userId
            }
            $.post("http://10.10.108.240:4000/users/logOut",data,(data,status)=>{
                if(data.loggedout===true){
                    localStorage.userId=null;
                    window.location.href="/";               
                }
            })}
    }
    render(){
        return(
            <CSSTransition 
            in={true}
            appear={true}
            timeout={500}
            classNames="down">
                <><nav className="navbar navbar-inverse navbar-fixed-top" style={{border:'solid',borderWidth:'0px 0px 1px 0px',boxShadow:'0pc 5px 5px #a8a8a8'}}>
                    <div className="container-fluid">
                        <div className="navbar-header">
                        <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar" style={{color:'black'}}>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>                        
                        </button>
                        <a className="navbar-brand" href="/">IDE YourCode</a>
                        </div>
                        <div className="collapse navbar-collapse" id="myNavbar">
                        <ul className="nav navbar-nav">
                            <li><a href="/compile">Compile</a></li>
                            <li onClick={()=>{if(this.state.loggedin===true) window.location.href="/contests"; else this.toggleLogin();}} style={{padding:'15px',color:'white',cursor:'pointer'}}>Contest</li>
                        </ul>
                        {this.state.loggedin===false?<ul className="nav navbar-nav navbar-right">

                            <li onClick={()=>{this.toggleSignUp();}} style={{padding:'15px',color:'white',cursor:'pointer'}}><span className="glyphicon glyphicon-user"></span> Sign Up</li>
                            <li onClick={()=>{this.toggleLogin();}} style={{padding:'15px',color:'white',cursor:'pointer'}}><span className="glyphicon glyphicon-log-in"></span> Login</li>
                        </ul>:<ul className="nav navbar-nav navbar-right">
                            {
                                this.state.username==='admin'?<li><a href='/addContest'><span className="glyphicon glyphicon-user"></span> Add Contest</a></li>:null
                            }
                            <li><a href='/profile'><span className="glyphicon glyphicon-user"></span> Profile</a></li>
                            <li onClick={()=>{this.toggleLogout();}}  style={{padding:'15px',color:'white',cursor:'pointer'}}><span className="glyphicon glyphicon-log-in"></span> LogOut</li>
                        </ul>}

                        </div>
                    </div>
                </nav>
                {this.state.showLogin?<Login close={this.toggleLogin.bind(this)} sign={this.toggleSignUp.bind(this)}/>:null}
				{this.state.showSignUp?<Signup close={this.toggleSignUp.bind(this)} login={this.toggleLogin.bind(this)}/>:null}
                </>
            </CSSTransition>
        )
    }
}
export default Example;