
import {CSSTransition} from 'react-transition-group';
import './App.css';
import React   from 'react';
class Example extends React.Component {
  constructor(props)
	{
	  super(props);
	  this.state={
	  };
  }


    render() {
      return (
        <div style={{position:"fixed",top:"0px",left:"0px",backgroundColor:"rgba(0,0,0,0.5)",width:"100%",height:"100%",zIndex:"1"}}>
            <div className="container-fluid decor_bg" id="login-panel">
                <div className="row">
                    <div className="col-lg-4 col-lg-offset-4 col-md-6 col-md-offset-3 col-sm-8 col-sm-offset-2">
                      <div className="container-fluid decor_bg" id="login-panel">
                      <CSSTransition
	                    in={true}
	                    appear={true}
	                    timeout={500}
	                    classNames="pull">
                         <div className="panel panel-primary" style={{marginTop:"20%",overflow:"auto",maxHeight:"80%"}}>
                             <div className="panel-heading" style={{"overflow":"hidden",marginTop:"-1px"}}>
                                 <h4 style={{"float":"left",display:"inline-block"}}>Code</h4><span style={{"float": "right"}} className="far fa-window-close fa-x btn" onClick={this.props.close}></span></div>
                             <div className="panel-body" id="pbody" style={{padding:"15px",minHeight:'400px'}}>
                             <textarea className='textbox' style={{width:'100%',height:'370px'}} value={this.props.code} readOnly></textarea>
                            </div>
                        </div>
                      </CSSTransition>
                      </div>
                </div>
            </div>
        </div>
       </div> 

       
      );
    }
  }
  export default Example;