import $ from 'jquery';
import './App.css';
import React   from 'react';
import Header from './header';
import Finger from 'fingerprintjs';
import Code from './code';
class Example extends React.Component {
    constructor(props)
	{
	  super(props);
	  this.state=
	  {
        loggedin:false,
        exe:0,
          validoPassword:-1,
          validnPassword:-1,
          mismatch:0,
          showCode:false,
          code:null,
          success:null
	  }
    }
    checkPattern(c){
        if(c===1){
		if(document.getElementById("npass")!=null){
		let password=document.getElementById("npass").value;
		let conPassword=document.getElementById("conPass").value;
		if(password.length>7&&password.length<101)
			this.setState({
				validnPassword:1,
				mismatch:password===conPassword?1:0
			});
		else
			this.setState({
				validnPassword:0,
				mismatch:password===conPassword?1:0
            });}
        }
        else{
            if(document.getElementById("npass")!=null){
                let password=document.getElementById("opass").value;
                if(password.length>7&&password.length<101)
                this.setState({
                    validoPassword:1
                });
                else
                this.setState({
                    validoPassword:0
                });
            }
        }
	}
    match(){
		if(document.getElementById("conPass")!=null){
		let password=document.getElementById("npass").value;
		let conPassword=document.getElementById("conPass").value;
		if(password===conPassword)
			this.setState({
				mismatch:0
			});
		else
			this.setState({
				mismatch:1
			});}
	}
    componentWillMount(){
        if((localStorage.userId==null?false:true)===true){
        let finger = new Finger().get();
        let data={
            client:finger,
            userid:localStorage.userId,

        }
        let pointer=this;
        $.post("http://10.10.108.240:4000/users/profile",data,(data,status)=>{
            if(data.loggedin===false){
                localStorage.userId=null;
                window.location.href="/";
                pointer.setState({
                    loggedin:false
                })                
            }
            else{
                
                pointer.setState({
                    loggedin:true,
                    username:data.username,
                    solutions:data.sol
                }) 
                
            }
        })}
    }
    change(){
        if(this.state.exe!==0)
        return;
        let finger = new Finger().get();
        let data={
            client:finger,
            userid:localStorage.userId,
            newPassword:document.getElementById("npass").value,
            oldPassword:document.getElementById("opass").value
        }
        let pointer=this;
        if(true){
            this.setState({
                exe:1
            })
        if(this.state.validnPassword===1&&this.state.validoPassword===1&&this.state.mismatch===0)
        $.post("http://10.10.108.240:4000/users/changePassword",data,(data,status)=>{

            if(data.error){
                pointer.setState({
                    message:data.msg,
                    success:null,
                    exe:0
                })
            }
            else{
                document.getElementById("conPass").value=document.getElementById("npass").value=document.getElementById("opass").value='';
                pointer.setState({
                    success:data.msg,
                    message:null,
                    exe:0
                })
            }
        })}
    }
    toggleCode(code){
        if(this.state.showCode){
            this.setState({
                showCode:false,
                code:null
            });
        }
        else{
            this.setState({
                showCode:true,
                code:code
            });
        }
    }
    render(){
        console.log(this.state)
        return(
            <div className='container'>
                <Header/>
                {this.state.loggedin?
                <div className='container' style={{marginTop:'75px'}}>
                    <div className="panel panel-primary">
                             <div className="panel-heading" style={{"overflow":"hidden",marginTop:"-1px"}}>
                                 <h4 style={{"float":"left",display:"inline-block"}}>Hello {this.state.username}</h4></div>
                             <div className="panel-body" id="pbody">
                        <form>
                            <label>Change Password: </label><br/>
                            <div className="form-group">
                                <input type="password" className="form-control" id="opass" defaultValue={null} placeholder="Old Password" onChange={()=>{this.checkPattern(0)}} pattern=".{8,}" name="password" style={{maxWidth:'350px'}}  required = {true}/>
                            </div>
                            {this.state.validoPassword===0?<p className="error">Invalid Password!!</p>:null}
                            <div className="form-group">
                                <input type="password" className="form-control" id="npass" defaultValue={null} placeholder="New Password" onChange={()=>{this.checkPattern(1)}} pattern=".{8,}" name="password" style={{maxWidth:'350px'}}  required = {true}/>
                            </div>
                            {this.state.validnPassword===0?<p className="error">Invalid Password!!<br/>Passwordlength must be between 8-100</p>:null}
                            <div className="form-group">
                                <input type="password" className="form-control" defaultValue={null} placeholder="Confirm New Password" id="conPass" onChange={()=>{this.match()}} pattern=".{8,}" name="password" style={{maxWidth:'350px'}} required = {true}/>
                            </div>
                            {this.state.mismatch!==0?<p className="error">Password mismatch!!</p>:null}
                            {this.state.exe===0?<div onClick={()=>{this.change()}} className="btn btn-danger" >Change Password</div>:<div className="btn btn-danger disabled" >Changing...</div>}
                            <br/>
                            <p className="error">{this.state.message}</p>
                            <p style={{color:'green'}}>{this.state.success}</p>
                        </form>
                        
                        </div>
                        <div className="panel-footer"><p>Hope you are liking Our Coding Platform :)<br/>Happy Hacking!!</p></div>
                    </div>
                    <h3>Your Solutions : </h3>
                    <table class="table">
                            <thead>
                                <tr>
                                <th scope="col">Solution id</th>
                                <th scope="col">Contest Id</th>
                                <th scope="col">Question Code</th>
                                <th scope="col">Score</th>
                                <th scope="col">Result</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.state.solutions.map(sol=>
                                <tr>
                                <th scope="row" onClick={()=>{this.toggleCode(sol.code)}} style={{cursor:'pointer'}}>{sol.solId} <i className="fas fa-external-link-alt"></i></th>
                                <td><a href={'./contest/'+sol.contest}>{sol.contest} <i className="fas fa-link"></i></a></td>
                                <td>{sol.ques}</td>
                                <td>{sol.score}</td>
                                <td>{sol.result==='AC'?<i style={{color:'green'}}>{sol.result}</i>:<i style={{color:'red'}}>{sol.result}</i>}</td>
                                </tr>)}
                            </tbody>
                        </table>
                        {this.state.showCode?<Code close={this.toggleCode.bind(this)} code={this.state.code}/>:null}
                </div>:null}
            </div>
        )
    }
}
export default Example;