import $ from 'jquery';
import './App.css';
import React   from 'react';
import Header from './header';
import Finger from 'fingerprintjs';
class Example extends React.Component {
    constructor(props)
	{
	  super(props);
	  this.state=
	  {
        loggedin:false,
        exe:0
	  }
    }
    componentWillMount(){
        if((localStorage.userId==null?false:true)===true){
        let finger = new Finger().get();
        let data={
            client:finger,
            userid:localStorage.userId
        }
        let pointer=this;
        $.post("http://10.10.108.240:4000/users/isLoggedIn",data,(data,status)=>{
            if(data.loggedin===false){
                localStorage.userId=null;
                window.location.href="/";
                pointer.setState({
                    loggedin:false
                })                
            }
            else{
                if(data.username==='admin')
                pointer.setState({
                    loggedin:true
                }) 
                
            }
        })}
    }
    add(){
        if(this.state.exe!==0)
        return;
        let finger = new Finger().get();
        let data={
            client:finger,
            userid:localStorage.userId,
            contestId:document.getElementById('conID').value,
            name:document.getElementById('name').value,
            img:document.getElementById('img').value,
            org:document.getElementById('org').value,
            startDate:document.getElementById('StartD').value,
            startTime:document.getElementById('StartT').value,
            endDate:document.getElementById('EndD').value,
            endTime:document.getElementById('EndT').value,
            head:document.getElementById('head').value,
            details:document.getElementById('det').value,
        }
        let pointer=this;
        if(document.getElementById('conID').value&&document.getElementById('name').value&&document.getElementById('img').value&&document.getElementById('org').value&&
        document.getElementById('StartD').value&&document.getElementById('StartT').value&&document.getElementById('EndD').value&&document.getElementById('EndT').value&&
        document.getElementById('head').value&&document.getElementById('det').value){
            this.setState({
                exe:1
            })
        $.post("http://10.10.108.240:4000/contest/add",data,(data,status)=>{

            if(data.error){
                pointer.setState({
                    message:data.msg,
                    exe:0
                })
            }
            else{
                pointer.setState({
                    exe:2
                })
            }
        })}
    }
    render(){
        return(
            <div className='container'>
                <Header/>
                {this.state.loggedin?
                <div style={{marginTop:'75px'}}>
                <div className="form-group">
                    <input type="text" id="conID" className="form-control" style={{maxWidth:'250px'}} placeholder="Enter Contest ID" />
                </div>
                <div className="form-group">
                    <input type="text" id="name" className="form-control" placeholder="Enter Contest Name" />
                </div>
                <div className="form-group">
                    <input type="text" id="img" className="form-control" placeholder="Enter Image Url" />
                </div>
                <div className="form-group">
                    <input type="text" id="org" className="form-control" placeholder="Enter Organiser Name" />
                </div>
                <div className="form-group">
                    <input type="text" id="head" className="form-control" placeholder="Enter Contest Head UserName" />
                </div>
                <div className="form-group">
                    <input type="text" id="det" className="form-control" placeholder="Enter Contest Details" />
                </div>
                <div className='row'>
                <div className="form-group col-sm-6">
                    <label htmlFor="st">Start Contest</label><br/>
                    <label htmlFor="s">Time :</label>
                    <input type="time" id="StartT" className="form-control" />
                    <label htmlFor="s">Date :</label>
                    <input type="date" id="StartD" className="form-control" />
                </div>
                <div className="form-group col-sm-6">
                    <label htmlFor="s">End Contest</label><br/>
                    <label htmlFor="s">Time :</label>
                    <input type="time" id="EndT" className="form-control" />
                    <label htmlFor="s">Date :</label>
                    <input type="date" id="EndD" className="form-control" />
                </div>
                </div>
                <div onClick={()=>{this.add()}} className={'btn btn-success'+(this.state.exe===0?'':' disabled')} style={{letterSpacing:'3px',fontSize:'20px'}}><i className="fas fa-plus"></i> {this.state.exe===1?'Adding...':this.state.exe===2?'Added':'Add'}</div>
                <p className="error">{this.state.message}</p>
                </div>:null}
            </div>
        )
    }
}
export default Example;