import $ from 'jquery';
import './App.css';
import React   from 'react';
import Header from './header';
import Finger from 'fingerprintjs';
class Example extends React.Component {
    constructor(props)
	{
	  super(props);
	  this.state=
	  {
          contest:{},
          score:0,
          edit:false,
          participate:false,
          date:new Date(),
        loggedin:localStorage.userId==null?false:true
	  }
    }
    componentWillMount(){
        if(this.state.loggedin===true){
        let finger = new Finger().get();
        let data={
            client:finger,
            userid:localStorage.userId
        }
        let pointer=this;
        $.post("http://10.10.108.240:4000/users/isLoggedIn",data,(data,status)=>{
            if(data.loggedin===false){
                localStorage.userId=null;
                pointer.setState({
                    loggedin:false
                });
                window.location.href="/";                
            }
            else{
                pointer.getContest();
            }
        })}
        else{
            window.location.href="/";
        }
    }
    getContest(){
        if(this.state.loggedin===true){
            let finger = new Finger().get();
            let data={
                client:finger,
                userid:localStorage.userId,
                contestId:this.props.match.params.id
            }
            let pointer=this;
            $.post("http://10.10.108.240:4000/contest/getContest",data,(data,status)=>{
                if(data.error&&data.msg==='Auth failed'){
                    localStorage.userId=null;
                    pointer.setState({
                        loggedin:false
                    });
                    window.location.href="/"; 
                }
                else{
                    data.contest.conSt=new Date(data.contest.startDate+' '+data.contest.startTime);
                    data.contest.conEnd=new Date(data.contest.endDate+' '+data.contest.endTime);
                     pointer.setState({
                         contest:data.contest,
                         edit:data.edit,
                         participate:data.participate,
                         score:data.score>0?data.score:0
                    });
                }
            })
        }
        
    }
    participate(){
        if(this.state.loggedin===true){
            let finger = new Finger().get();
            let data={
                client:finger,
                userid:localStorage.userId,
                contestId:this.props.match.params.id
            }
            let pointer=this;
            $.post("http://10.10.108.240:4000/contest/participate",data,(data,status)=>{
                if(data.error&&data.msg==='Auth failed'){
                    localStorage.userId=null;
                    pointer.setState({
                        loggedin:false
                    });
                    window.location.href="/"; 
                }
                else{
                    if(data.error){
                        alert(data.msg);
                    }
                    else{
                        this.getContest();
                    }
                }
            });
        }
    }
    remove(quesCode){
        let finger = new Finger().get();
            let data={
                client:finger,
                userid:localStorage.userId,
                contestId:this.props.match.params.id,
                quesCode:quesCode
            }
            let pointer=this;
            $.post("http://10.10.108.240:4000/contest/removeQuestion",data,(data,status)=>{
                if(data.error){
                    alert(data.msg);
                }
                else{
                    pointer.getContest()
                }
        })
    }
    add(){
        let finger = new Finger().get();
            let data={
                client:finger,
                userid:localStorage.userId,
                contestId:this.props.match.params.id,
                quesCode:document.getElementById('quesCode').value,
                input:document.getElementById('in').value,
                output:document.getElementById('out').value,
                ques:document.getElementById('ques').value,
                testin:document.getElementById('testin').value,
                testout:document.getElementById('testout').value,
                constraints:document.getElementById('contraints').value,
                example:document.getElementById('ex').value,
                maxScore:document.getElementById('max').value
            }
            let pointer=this;
            $.post("http://10.10.108.240:4000/contest/addQuestion",data,(data,status)=>{
                if(data.error){
                    alert(data.msg);
                }
                else{
                    pointer.getContest();
                    document.getElementById('quesCode').value=document.getElementById('in').value=document.getElementById('out').value='';
                    document.getElementById('ques').value=document.getElementById('testin').value=document.getElementById('testout').value=document.getElementById('contraints').value=document.getElementById('ex').value='';
                }
        })
    }
    componentDidMount(){
        $("textarea").keydown(function(e) {
            if(e.keyCode === 9) { // tab was pressed
                // get caret position/selection
                var start = this.selectionStart;
                var end = this.selectionEnd;
        
                var $this = $(this);
                var value = $this.val();
        
                // set textarea value to: text before caret + tab + text after caret
                $this.val(value.substring(0, start)
                            + "\t"
                            + value.substring(end));
        
                // put caret at right position again (add one for the tab)
                this.selectionStart = this.selectionEnd = start + 1;
        
                // prevent the focus lose
                e.preventDefault();
            }
        });
    }
    render(){
        return(
            <div>
                <Header/>
                <div style={{paddingTop:'50px'}}>
                    <div className='col-sm-4 col-md-3' style={{backgroundColor:'black',height:window.screen.width>420?'100%':'auto',position:window.screen.width>420?'fixed':'static',overflow:'auto'}}>
                        <br/><h1 style={{color:'#1e7bfc'}}>{this.state.contest.name}</h1>
                        <img src={this.state.contest.img} alt={this.state.contest.name} style={{height:'auto',width:'100%'}}/>
                        <h3 style={{color:'white'}}>Organised by {this.state.contest.org}</h3>
                        <p style={{color:'white'}}>Start at :<br/>{String(this.state.contest.conSt)}<br/><br/>
                        End at :<br/>{String(this.state.contest.conEnd)}<br/><br/>
                        Contest Head : {this.state.contest.head}
                        <br/><br/>
                        {this.state.edit?<><a href={'/contest/'+this.props.match.params.id+'/leaderboard'}><div className='btn btn-primary' style={{backgroundColor:'#337ab7',letterSpacing:'3px',fontSize:'20px',textAlign:'center',width:'100%'}}>LeaderBoard</div></a>
                        <br/><br/></>:null}
                        {this.state.contest.details}
                        <br/>
                        <br/>
                        <br/>
                        <br/></p>

                    </div>
                    {this.state.edit?
                        <div className='col-sm-8 col-sm-offset-4 col-md-9 col-md-offset-3'>
                        {this.state.contest.questions.map(question=>
                            <div style={{overflow:'hidden',marginTop:'10px',marginBottom:'10px',border:'solid',padding:'5px',borderRadius:'6px'}}>
                                <div style={{overflow:'hidden'}}><span style={{float:'left'}}>Question Code : {question.quesCode}</span><span style={{float:'right'}}>Question Score : {question.maxScore}</span></div>
                                <div>
                                    <h4>Question : </h4>
                                    <p>{question.ques}</p>
                                </div><br/>
                                <div className='row'>
                                <div className='col-sm-6'>
                                    <h4>Input : </h4>
                                    <p className='textbox' style={{width:'100%',height:'100px'}}>{question.input}</p>
                                </div>
                                <div className='col-sm-6'>
                                    <h4>Output : </h4>
                                    <p className='textbox' style={{width:'100%',height:'100px'}}>{question.output}</p>
                                </div>
                                </div><br/>
                                <div className='row'>
                                <div className='col-sm-6'>
                                    <h4>Test Input : </h4>
                                    <textarea style={{width:'100%',height:'150px'}} value={question.testin} readOnly></textarea>
                                </div>
                                <div className='col-sm-6'>
                                    <h4>Test Output : </h4>
                                    <textarea style={{width:'100%',height:'150px'}} value={question.testout} readOnly></textarea>
                                </div>
                                </div><br/>
                                <div>
                                    <h4>Contraints : </h4>
                                    <p className='textbox' style={{width:'100%',height:'75px'}}>{question.constraints}</p>
                                </div><br/>
                                <div>
                                    <h4>Example : </h4>
                                    <p className='textbox'>{question.example}</p>
                                </div>
                                <span  onClick={()=>{this.remove(question.quesCode)}} className={'btn btn-danger'} style={{float:'right'}}>Remove</span>
                            </div>
                        )}
                        <div style={{overflow:'hidden',marginTop:'10px',marginBottom:'10px',border:'solid',padding:'5px',borderRadius:'6px'}}>
                                <div style={{overflow:'hidden'}}><span style={{float:'left'}}>Question Code : <input type="text" id="quesCode" className="form-control" style={{maxWidth:'250px'}} placeholder="Enter Question ID" /></span>
                                <span style={{float:'right'}}>Question Score : <input type="number" id="max" className="form-control" style={{maxWidth:'250px'}} placeholder="Enter Question Score" /></span></div><br/><br/>
                                <div>
                                    <h4>Question : </h4>
                                    <div className="form-group">
                                        <input type="text" id="ques" className="form-control" placeholder="Enter question" />
                                    </div>
                                </div>
                                <br/>
                                <div className='row'>
                                <div className='col-sm-6'>
                                    <h4>Input : </h4>
                                    <textarea style={{width:'100%',height:'100px'}} id='in'></textarea>
                                </div>
                                <div className='col-sm-6'>
                                    <h4>Output : </h4>
                                    <textarea style={{width:'100%',height:'100px'}} id='out'></textarea>
                                </div>
                                </div><br/>
                                <div className='row'>
                                <div className='col-sm-6'>
                                    <h4>Test Input : </h4>
                                    <textarea style={{width:'100%',height:'150px'}} id='testin'></textarea>
                                </div>
                                <div className='col-sm-6'>
                                    <h4>Test Output : </h4>
                                    <textarea style={{width:'100%',height:'150px'}} id='testout'></textarea>
                                </div>
                                </div><br/>
                                <div>
                                    <h4>Contraints : </h4>
                                    <textarea style={{width:'100%',height:'75px'}} id='contraints'></textarea>
                                </div><br/>
                                <div>
                                    <h4>Example : </h4>
                                    <textarea style={{width:'100%',height:'100px'}} id='ex'></textarea>
                                    
                                </div>
                                <span  onClick={()=>{this.add()}} className={'btn btn-success'} style={{float:'right'}}>Add</span>
                        </div>

                        </div>

                    :
                    <div className='col-sm-8 col-sm-offset-4 col-md-9 col-md-offset-3'>
                        <div style={{width:'100%',backgroundColor:'#dedede',overflow:'hidden',padding:'15px',borderRadius:'0px 0px 10px 10px'}}>
                            <span style={{float:'left',padding:'8px'}}>Status : {this.state.participate?this.state.date<this.state.contest.conSt?' Not yet started':this.state.date>this.state.contest.conEnd?"Already Ended":'Ongoing':' Not Participated'}</span>
                            {
                                this.state.participate?<span style={{float:'right',color:'#ffbf00',fontWeight:'600',fontSize:'20px',padding:'3px'}}>Score : {this.state.score}</span>:
                                <span onClick={()=>{this.participate()}} className={'btn btn-primary'} style={{backgroundColor:'#337ab7',float:'right'}}><i className="fas fa-sign-in-alt"></i> Participate</span>
                            }
                        </div>
                        {
                                this.state.participate?this.state.date<this.state.contest.conSt?
                                <div style={{paddingLeft:'10%',paddingRight:'10%'}}><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
                                    <h1 style={{textAlign:'center',color:'green'}}>Questions will be available as soon as the Contest starts.</h1>
                                    <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
                                </div>
                                :this.state.date>this.state.contest.conEnd?
                                <div style={{paddingLeft:'10%',paddingRight:'10%'}}><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
                                    <h1 style={{textAlign:'center',color:'red'}}>Contest has already Ended.</h1>
                                    <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
                                </div>
                                :
                                this.state.contest.questions.map(question=>
                                    <div style={{overflow:'hidden',marginTop:'10px',marginBottom:'10px',border:'solid',padding:'5px',borderRadius:'6px'}}>
                                        <div style={{overflow:'hidden'}}><span style={{float:'left'}}>Question Code : {question.quesCode}</span><span style={{float:'right'}}>Question Score : {question.maxScore}</span></div>
                                        <div>
                                            <h4>Question : </h4>
                                            <p>{question.ques}</p>
                                        </div><br/>
                                        <div className='row'>
                                        <div className='col-sm-6'>
                                            <h4>Input : </h4>
                                            <p className='textbox' style={{width:'100%'}}>{question.input}</p>
                                        </div>
                                        <div className='col-sm-6'>
                                            <h4>Output : </h4>
                                            <p className='textbox' style={{width:'100%'}}>{question.output}</p>
                                        </div>
                                        </div><br/>
                                        <div>
                                            <h4>Contraints : </h4>
                                            <p className='textbox' style={{width:'100%'}}>{question.constraints}</p>
                                        </div><br/>
                                        <div>
                                            <h4>Example : </h4>
                                            <p className='textbox'>{question.example}</p>
                                        </div>
                                        <a href={'/contest/'+this.state.contest.contestId+'/submit/'+question.quesCode} className={'btn btn-success'} style={{float:'right'}}>Submit</a>
                                    </div>
                                )
                                :
                                <div style={{paddingLeft:'10%',paddingRight:'10%'}}><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
                                    <h1 style={{textAlign:'center',color:'green'}}>Participate in the contest to solve some awsome coding problems.</h1>
                                    <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
                                </div>
                        }
                    </div>}
                </div>
            </div>
        )
    }
}
export default Example;