import './App.css';
import Header from './header';
import React  from 'react';
import Foot from './footer';
class Example extends React.Component {
    render(){
        return(
            <div style={{marginTop:'50px',height:'100%'}}>
            <Header/>
            <div style={{backgroundImage:'url("https://images.yourstory.com/cs/wordpress/2018/03/coding-job.png?fm=png&auto=format")',backgroundPosition:'center',backgroundRepeat:'no-repeat',backgroundSize:'cover',float:'left',width:'100%',height:'100%',overflow:'hidden'}}>
                <div className='col-sm-6' style={{padding:'100px',fontSize:'6em',letterSpacing:'10px',color:'white'}}><br/>
                    IDE YourCode
                </div>
                <div className='col-sm-6' style={{padding:'150px 50px 150px 250px',fontSize:'4em',letterSpacing:'10px',color:'white'}}><br/>
                    Compile<br/><br/>
                    Participate<br/><br/>
                    Win
                </div>
            </div>
            <div style={{marginTop:'50px'}}>
                <center><h1><br/>Languages Supported</h1></center>
                <div style={{overflow:'hidden'}}>
                    <div className='col-md-3 col-sm-4' style={{height:"300px",overflow:'hidden'}}>
                        <img src='https://toppng.com/public/uploads/preview/c-programming-icon-c-programming-language-logo-11562945679duaxtn3yq0.png' style={{width:'100%',height:'auto',padding:'50px'}} alt='c'/>
                    </div>
                    <div className='col-md-3 col-sm-4' style={{height:"300px",overflow:'hidden'}}>
                    <img src='https://www.trzcacak.rs/myfile/detail/368-3682519_download-c-png-image-c-c-programming-logo.png' style={{width:'100%',height:'auto',padding:'50px'}}  alt='c++'/>
                    </div>
                    <div className='col-md-3 col-sm-4' style={{height:"300px",overflow:'hidden'}}>
                    <img src='https://freepngimg.com/thumb/python_logo/5-2-python-logo-png-image-thumb.png' style={{width:'100%',height:'auto',padding:'50px'}}  alt='Python'/>
                    </div>
                    <div className='col-md-3 col-sm-4' style={{height:"300px",overflow:'hidden'}}>
                    <img src='https://p7.hiclipart.com/preview/837/18/530/logo-java-runtime-environment-programming-language-runtime-system-oracle-thumbnail.jpg' style={{width:'100%',height:'auto',padding:'50px'}}  alt='c'/>
                    </div>
                </div>
            </div><Foot/>
            </div>
        )
    }
}
export default Example;