import $ from 'jquery';
import './App.css';
import React   from 'react';
import Header from './header';
import Foot from './footer';
class Example extends React.Component {
    
    constructor(props)
	{
	  super(props);
	  this.state=
	  {
          output:null,
          color:'white',
          exe:0
	  }
	}

    run(){
        this.setState({
            exe:1
        })
        let data={
            code:document.getElementById("code").value,
            input:document.getElementById("input").value,
            lang:document.querySelector('input[name="lang"]:checked').value
        }
        let pointer=this;
        $.post("http://10.10.108.240:4000/solution/run",data,(data,status)=>{
              if(data.error===false){
                pointer.setState({
                    output:data.stdout,
                    color:'white',
                    exe:0
                });
               }
               else{ 
                pointer.setState({
                    output:data.msg,
                    color:'red',
                    exe:0
                });
               }
        });
    }
    componentDidMount(){
        document.getElementById('langc').checked=true;
        $("textarea").keydown(function(e) {
            if(e.keyCode === 9) { // tab was pressed
                // get caret position/selection
                var start = this.selectionStart;
                var end = this.selectionEnd;
        
                var $this = $(this);
                var value = $this.val();
        
                // set textarea value to: text before caret + tab + text after caret
                $this.val(value.substring(0, start)
                            + "\t"
                            + value.substring(end));
        
                // put caret at right position again (add one for the tab)
                this.selectionStart = this.selectionEnd = start + 1;
        
                // prevent the focus lose
                e.preventDefault();
            }
        });
    }
    render(){
        return(
            <><div style={{marginTop:'50px',overflow:'hidden'}}>
                <Header/>
                <center><h1 className='headings'>Welcome to YourCode Compiler</h1></center>
                <div className='col-md-6'>
                    <h3>Your Code :</h3>
                    <textarea style={{height:'600px',width:'100%',padding:'10px',borderRadius:'10px',border:'solid',borderWidth:'1px'}} id='code'></textarea>
                </div>
                <div className='col-md-6'>
                    <div className='col-sm-6'>
                        <h3>Your Input :</h3>
                        <textarea style={{height:'275px',width:'100%',padding:'10px',borderRadius:'10px',border:'solid',borderWidth:'1px'}} id='input'></textarea>
                    </div>
                    <div className='col-sm-6'>
                        <h3>Your Language :</h3>
                        <p style={{paddingTop:'10px'}}>
                        <input type="radio" id="langc" name='lang' value="c" /> C<br/><br/>
                        <input type="radio" id="langcpp" name='lang' value="cpp"/> C++ 14<br/><br/>
                        <input type="radio" id="langpy2" value="py2" name='lang'/> Python 2.7<br/><br/>
                        <input type="radio" id="langpy3" name='lang' value="py3"/> Python 3.6<br/><br/>
                        <input type="radio" id="langjava" value="java" name='lang'/> Java
                        </p><br/>
                        <div onClick={()=>{this.run()}} className={'btn btn-primary'+(this.state.exe===0?'':' disabled')} style={{backgroundColor:'#337ab7',letterSpacing:'3px',fontSize:'20px'}}><i class="far fa-play-circle"></i> Execute</div>
                    </div>
                    <div className='col-sm-12'>
                        <h3>Your Output :</h3>
                        <textarea style={{height:'275px',width:'100%',padding:'10px',borderRadius:'10px',border:'solid',borderWidth:'1px',fontWeight:'600',color:this.state.color,backgroundColor:'black'}} id='output' value={this.state.output} readOnly></textarea>
                    
                    </div>
                </div>
                
            </div><Foot/></>
        )
    }
}
export default Example;